import React, { useEffect }  from 'react'
import { Link ,useNavigate} from 'react-router-dom'
import { useSelector, useDispatch } from "react-redux";
import decode from "jwt-decode";
import logo from '../../assessts/logo.png'
import search from '../../assessts/search.png'
import Avatar from '../../components/Navbar/Avatar/Avatar'
import { setCurrentUser } from "../../actions/currentUser";
import './Navbar.css'

const Navbar = ({handleSlideIn}) => {
  const dispatch = useDispatch();
  var User = useSelector((state) => state.currentUserReducer);
  const navigate = useNavigate();

  const handleLogout = () => {
  dispatch({ type: "LOGOUT" });
  navigate("/");
  dispatch(setCurrentUser(null));
};

useEffect(() => {
  const token = User?.token;
  if (token) {
    const decodedToken = decode(token);
    if (decodedToken.exp * 1000 < new Date().getTime()) {
      handleLogout();
    }
  }
  dispatch(setCurrentUser(JSON.parse(localStorage.getItem("Profile"))));
}, 
[User?.token, dispatch]);

  return (
    <nav className='main-nav'>
      <div className='navbar'>
        <div className='navbar-1'>
        <Link to='/'className='nav-item nav-logo'>
            <img src={logo} alt='logo' width={'200'}/>
        </Link>
        <Link to='/' className='nav-item nav-btn'>About</Link>
        <Link to='/' className='nav-item nav-btn'>Products</Link>
        <Link to='/' className='nav-item nav-btn'>For teams</Link>
        <Link to='/Plan' className='nav-item nav-btn'>Make Payment</Link>
        <form>
          <input type="text" placeholder='Search..'/>
            <img src={search} alt='search' width={'20'} className='search-icon'/>
        </form>
        </div>
        <div className='navbar-2'>
        {User === null ?(
        <Link to='/Auth' className='nav-item nav-links'>Log In</Link> ):(
        <>
        <Avatar backgroundColor='#009dff' px="16px" py="10px" borderRadius="80%" color='white' cursor='pointer' fontSize="18px">
        <Link
                  to={`/Users/${User?.result?._id}`}
                  style={{ color: "white", textDecoration: "none" }}
                >
                  {User.result.name.charAt(0).toUpperCase()}
                </Link>
                </Avatar>
              <button className="nav-item-nav-links" onClick={handleLogout}>
                Log out
              </button>
           </>
        )}
        </div> 
      </div>
    </nav>
  )
}

export default Navbar

